import React, { Component } from "react";
import "../exchange.css";
import exchangeImg from "../images/exchange2.png";
import poland from "../images/poland.png";
// import usa from "../images/united-states.png";
import sweden from "../images/sweden.png";
import norge from "../images/norway.png";
import uk from "../images/united-kingdom.png";
import denmark from "../images/denmark.png";
import russia from "../images/russia.png";
import korea from "../images/south-korea.png";
// import wallstreet from "../images/wallstreet.jpg";

class Exchange extends Component {
  state = {
    amount: 0.0,
    date: "",
    currency: "",
    base: "",
    disabled: true,
  };

  handleExchange = (event) => {
    this.setState({
      amount: event.target.value,
    });
    event.preventDefault();
  };
  handleApi = () => {
    fetch("https://api.exchangeratesapi.io/latest")
      .then((response) => response.json())
      .then((fetchData) =>
        this.setState({
          currency: fetchData.rates,
          date: fetchData.date,
          base: fetchData.base,
          disabled: false,
          dollar: "dollar",
        })
      );
  };

  render() {
    const pln = (
      this.state.amount *
      (this.state.currency.PLN / this.state.currency.USD)
    ).toFixed(2);
    const gbp = (
      this.state.amount *
      (this.state.currency.GBP / this.state.currency.USD)
    ).toFixed(2);
    const sek = (
      this.state.amount *
      (this.state.currency.SEK / this.state.currency.USD)
    ).toFixed(2);
    const nok = (
      this.state.amount *
      (this.state.currency.NOK / this.state.currency.USD)
    ).toFixed(2);
    const dkk = (
      this.state.amount *
      (this.state.currency.DKK / this.state.currency.USD)
    ).toFixed(2);
    const rub = (
      this.state.amount *
      (this.state.currency.RUB / this.state.currency.USD)
    ).toFixed(2);
    const krw = (
      this.state.amount *
      (this.state.currency.KRW / this.state.currency.USD)
    ).toFixed(2);
    console.log("USD CONVERTER created by Artur Lewandowicz ");
    return (
      <div className="parentDiv">
        <div className="wrap"></div>
        <div className="wrap">
          <img src={exchangeImg} alt="" />
          <p>USD Converter</p>
          <h1>
            {this.state.amount}
            <span className="sign">$</span>{" "}
            {this.state.amount < 2 ? "dollar" : "dollars"} is :
          </h1>
          <div className="field">
            <input
              type="number"
              value={this.state.amount}
              onChange={this.handleExchange}
              min="0"
              max="1000000"
              placeholder="Your amount in euro"
              disabled={this.state.disabled}
            />
            <button
              onClick={this.handleApi}
              className="downloadBtn"
              type="submit"
            >
              START
            </button>
          </div>
          {this.state.date <= 0
            ? "Press start to begin"
            : `Updated: ${this.state.date}`}
          <div className="currencyApp">
            <div className="currencies">
              <div>
                <img src={poland} alt="" />
                PLN: <span>{this.state.amount === 0 ? "" : pln}</span>
                <p>Polish Zloty</p>
              </div>
              <div>
                <img src={uk} alt="" />
                GBP: <span>{this.state.amount === 0 ? "" : gbp}</span>
                <p>British Pound</p>
              </div>
              <div>
                <img src={sweden} alt="" />
                SEK: <span>{this.state.amount === 0 ? "" : sek}</span>
                <p>Swedish Krona</p>
              </div>
              <div>
                <img src={norge} alt="" />
                NOK: <span>{this.state.amount === 0 ? "" : nok}</span>
                <p>Norwegian Krone</p>
              </div>
              <div>
                <img src={denmark} alt="" />
                DKK: <span>{this.state.amount === 0 ? "" : dkk}</span>
                <p>Danish Krone</p>
              </div>
              <div>
                <img src={russia} alt="" />
                RUB: <span>{this.state.amount === 0 ? "" : rub}</span>
                <p>Russian Ruble</p>
              </div>
              <div>
                <img src={korea} alt="" />
                KRW: <span>{this.state.amount === 0 ? "" : krw}</span>
                <p>South Korean Won</p>
              </div>
            </div>
          </div>{" "}
          <p>
            <code>API:</code>https://api.exchangeratesapi.io/latest
          </p>
        </div>
        <div className="wrap"></div>
      </div>
    );
  }
}

export default Exchange;
